.welcome-page_container {
    position: relative;
    width: 100%;
    min-height: 100vh;

    display: flex;
    justify-content: space-between;
    align-items: center;

    background-color: #051937;
    background-size: contain;
    background-repeat: no-repeat;
}

.particles {
    z-index: 4;
    height: 100vh;
}

.description_container {
    width: 50%;
    margin: 220px 0 0 30px;
    padding: 0 20px 40px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.header_welcome {
    color: #fff;
    text-align: center;
    font-size: 1.75vw;
    line-height: 3vw;
    margin: 0;
}

.header_welcome span {
    color: #27d4cf;
    font-size: 1.8em;
}

.welcome-description_container {
    border: 1px solid #fff;
    border-radius: 25px;    
    padding: 1.5vw;
    margin: 1vw 0;
}

.p_welcome-description {
    color: #fff;
    padding: 0;
    margin: 0;
    text-align: center;
    font-size: 1.5vw;
    line-height: 1.5vw;
}

.p_quote {
    color: #27d4cf;
    text-align: center;
    padding: 0;
    margin: 0;
    font-size: 1.5vw;
    line-height: 1.5vw;
    text-transform: uppercase;
    margin-top: 1vw;
    padding-top: 1vw;
    border-top: 1px solid #fff;
}

.link_about {
    position: relative;
    background: none;
    border: none;
    margin: 0;
    padding: 1vw;
    text-decoration: none;
    color: #fff;
    letter-spacing: 2px;
    font-size: 1.5vw;
    font-weight: 300;
    transition: .5s;
    cursor: pointer;
    z-index: 5;
}

.link_about:hover {
    background: #fff;
    color: #000;
    border-radius: 25px;
}

.link_about span {
    margin: 0;
    padding: 0;
}

.profile-picture_container {
    margin-top: 220px;
    padding: 0;
    z-index: 5;
}

.img_profile-picture {
    height: 35vw;
    width: auto;
    border-radius: 50px;
    margin-bottom: 40px;
    opacity: .75;
}

.ul_social-media {
    list-style-type: none;
    z-index: 5;
    padding: 0 20px 40px 20px;
    margin: 220px 0 0 0;
}

.ul_social-media li {
    background: #fff;
    border-radius: 10px;
    margin: .8vw 0;
    display: flex;
    align-items: center;
    transition: .5s;
    opacity: .45;
}

.ul_social-media:hover li {
    opacity: .85;
}

.ul_social-media li a {
    height: 2.8vw;
    width: 3vw;
}

.icon_social-media_container {
    background: linear-gradient(89deg, #CFD6E6 0%, #E7EFF9  100%);
    /* box-shadow: 10px 10px #000; */
}

.icon_social-media {
    height: 2.2vw;
    width: auto;
    margin: .3vw .4vw .1vw .4vw;
}

.icon_social-media:hover {
    cursor: pointer;
}

@media only screen and (max-width: 1200px) {
    .welcome-page_container {
        position: relative;
        width: 100%;
        min-height: calc(100vh + 100px);
        padding-top: 40px;
        padding-bottom: 100px;

        display: flex;
        flex-direction: column-reverse;
        justify-content: space-between;
        align-items: center;
    
        background-color: #051937;
        background-size: contain;
        background-repeat: no-repeat;
    }

    .description_container {
        width: 100%;
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .header_welcome {
        color: #fff;
        height: 15vh;
        text-align: center;
        font-size: 8vw;
        line-height: 10vw;
        margin: 0;
    }
    
    .header_welcome span {
        color: #27d4cf;
        font-size: 10vw;
    }
    
    .welcome-description_container {
        min-height: 50vh;
        border: 1px solid #fff;
        border-radius: 25px;    
        padding: 3vw;
        margin: 20vw 0 5vh;
    }
    
    .p_welcome-description {
        color: #fff;
        padding: 0;
        margin: 0;
        text-align: center;
        font-size: 5vw;
        line-height: 5vw;
    }
    
    .p_quote {
        color: #27d4cf;
        text-align: center;
        padding: 0;
        margin: 0;
        font-size: 5vw;
        line-height: 5vw;
        text-transform: uppercase;
        margin-top: 2vh;
        padding-top: 2vh;
        border-top: 1px solid #fff;
    }
    
    .link_about {
        position: relative;
        background: none;
        border: none;
        margin: 0;
        padding: 3vw;
        text-decoration: none;
        color: #fff;
        letter-spacing: 2px;
        font-size: 5vh;
        font-weight: 300;
        transition: .5s;
        cursor: pointer;
        z-index: 5;
    }
    
    .link_about:hover {
        background: #fff;
        color: #000;
        border-radius: 25px;
    }
    
    .link_about span {
        margin: 0;
        padding: 0;
    }

    .profile-picture_container {
        margin: 5vh 0;
        padding: 0;
        z-index: 5;
    }
    
    .img_profile-picture {
        height: 70vh;
        margin: 0;
        width: auto;
        border-radius: 50px;
        opacity: .75;
    }
    
    .ul_social-media {
        display: none;
    }
}